.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap:15%;
    margin:0rem 0.5rem;
}
.about_me{
width : 100%;
aspect-ratio: 1 / 1;
border-radius:1rem;
background : linear-gradient(45deg,transparent,var(--color-primary),transparent);
display:grid;
place-items: center;
}
.about_me-image{
    width:100%;
    height:100%;
    border-radius:1rem;
    overflow:hidden;
    transform:rotate(0deg);
    transition: var(--transition);
}
.about_me-image:hover{
    transform: rotate(10deg);
    transition: var(--transition);
}
.about_cards{
    display:grid;
    grid-template-columns: repeat(3,1fr);
    gap:1.5rem;

}
.about_card{
    background:var(--color-bg-variant) ;
    border:1px solid transparent;
    border-radius: 1rem;
    padding:2rem;
    text-align:center;
    transition: var(--transition);
}
.about_card:hover{
    background:transparent;
    border-color:var(--color-primary-variant);
    cursor: default;
}
.about_icons{
    color:var(--color-primary);
    font-size:1.4rem;
    margin-bottom:1rem;

}
.about_card h5{
font-size:0.95rem;
}
.about_card small{
    font-size:0.7rem;
    color: var(--color-light);

}
.about_content p{
    margin:2rem 0 2.6rem;
    color:var(--color-light);
}





/*====================== Media Querries for tablets  ======================*/
@media screen and (max-width:1024px){
    .about_container{
        grid-template-columns:1fr;
        gap:0;

    }
    .about_me{
        width:50%;
        margin:2rem auto 4rem;
    }
    .about_content p{
        margin:1rem 0 1.5rem;
    }
}
 /*====================== Media Querries for phones  ======================*/

 @media screen and (max-width:600px){
    .about_me{
        width:85%;
        margin:0 auto 3rem;  
    }
    .about_cards{
        grid-template-columns:1 fr 1 fr;
        gap: 1rem;
    }
    .about_content{
        text-align:center;

    }
    .about_content p{
        margin:1.5rem 0.5rem;
    }
 }
 
 
 @media  (max-width:450px){
    .about_container{
        display: flow-root ;
       
    }
    #content{
        margin: 1rem 0;
    }
    #cards{
        display: inline;
        margin: 1rem 0;
    }
    .about_card{
        margin: 1rem;
    }
}