@import url('https://fonts.googleapis.com/css2?family=Neonderthaw&family=Poppins:wght@300;400;500;600&display=swap');

*{
  margin:0;
  padding:0;
  
  border: 0;
  outline: 0;
  box-sizing: 0;
  list-style: none;
  text-decoration: none;
}
:root{
   --color-bg: #1f1f38;
   /* --color-bg: #000000; */
   --color-bg-variant:#2c2c6c;
   --color-primary: #4db5ff;
   /* --color-primary: #2B2B2B; */
   --color-primary-variant: rgba(77, 181, 255, @.4);
   --color-white: #fff;
   --color-light: rgba(255, 255, 255, 0.6);
   
   --transition: all 400ms ease;

   --contaier-width-lg:75%;
   --contaier-width-md:86%;
   --container-width-ms:90%;
}
html{
  scroll-behavior: smooth;
}
::-webkit-scrollbar{
  display: none;
}
body{
  font-family: 'Poppins', sans-serif;
  background-color: var(--color-bg);
  color:var(--color-white);
  line-height:1.7;
  background-image:url(../src/assets/bg-texture.png);
}
/*================== Genral Style =======================*/
.container{
  width : var(--container-width-lg);
  margin:0 3rem;
  
}
/* .header_container{
  text-align: center;
} */
h1,h2,h3,h4,h5{font-weight: 500 ;}
h1{font-size:2.5rem;}
/* section{*/
  /* margin-top:8rem;
  height:100vh; */
/* }  */ 
section > h2,
section > h5{
  text-align:center;
  color:var(--color-light);
}
section>h2{
  color: var(--color-primary);
}
.text-light{
  color:var(--color-light);
}
a{
  color:var(--color-primary);
  transition:var(--transition);
}
a:hover{color:var(--color-white);}
.btn{
  width:max-content;
  display:inline-block;
  color:var(--color-primary);
  padding:0.75rem 1.2rem;
  border-radius:0.4rem;
  cursor:pointer;
  border:1px solid var(--color-primary);
  transition:var(--transition);
}
.btn:hover{
  background:var(--color_white);
  color:var(--color_bg);
  border-color:transparent; 
}
.btn-primary{
  background:var(--color-primary);
  color:var(--color-bg);
}
img{
  display:block;
  width:100%;
  object-fit:cover;
  }
  /*====================== Media Querries for phones  ======================*/
  @media screen and (max-width:600px){
    .container{
      width:var(--container-width-ms);
    }
    section>h2{margin: top 2rem;
    }
  }
/*====================== Media Querries for tablets  ======================*/
  @media screen and (max-width:1024px){
    .container{
      width:var(--container-width-md);
    }
    section>h2{margin: top 6rem;
    }
  }
  